"use client";

import React from "react";
import Link from "next/link";
import {
  Box,
  Container,
  Grid,
  Typography,
  TextField,
  Button,
  Stack,
  Divider,
  IconButton,
} from "@mui/material";
import {
  Facebook,
  Twitter,
  LinkedIn,
  YouTube,
  Instagram,
  Email,
  Phone,
  ContactMail,
} from "@mui/icons-material";

const Footer = () => {
  // Remove useMediaQuery hooks or make them conditional
  const isMobile = typeof window !== 'undefined' ? window.innerWidth < 900 : false;
  const isSmallMobile = typeof window !== 'undefined' ? window.innerWidth < 600 : false;

  const footerLinks = [
    {
      title: "About Us",
      links: [
        { label: "About Us", href: "/about-us" },
        { label: "Privacy Policy", href: "/privacy-policy" },
        { label: "File A Claim", href: "/file-claim" },
      ],
    },
    {
      title: "Contact Us",
      links: [
        { label: "Contact Us", href: "/contact-us" },
        { label: "Become A Supplier", href: "/become-supplier" },
        { label: "Careers And Jobs", href: "/careers" },
      ],
    },
    {
      title: "Terms Of Use",
      links: [
        { label: "Terms Of Use", href: "/terms-of-use" },
        { label: "FAQ", href: "/faq" },
        { label: "How To Book", href: "/how-to-book" },
      ],
    },
    {
      title: "Policies & Tips",
      links: [
        { label: "Cookies Policy", href: "/cookies-policy" },
        { label: "Booking Tips", href: "/booking-tips" },
      ],
    },
  ];

  const socialIcons = [
    { icon: <Facebook />, color: "#1877F2" },
    { icon: <Twitter />, color: "#1DA1F2" },
    { icon: <LinkedIn />, color: "#0077B5" },
    { icon: <YouTube />, color: "#FF0000" },
    { icon: <Instagram />, color: "#E4405F" },
  ];

  return (
    <Box
      component="footer"
      sx={{
        borderTop: "1px solid",
        borderColor: "grey.200",
        color: "grey.800",
        backgroundColor: "white",
        py: 4,
      }}
    >
      <Container maxWidth="xl">
        {/* Top Section: 4 Columns Grid */}
        <Grid container spacing={4} sx={{ mb: 6 }}>
          {footerLinks.map((column, index) => (
            <Grid
              item
              xs={12}
              sm={6}
              md={3}
              key={index}
              sx={{
                display: "flex",
                flexDirection: "column",
              }}
            >
              <Typography
                variant="subtitle2"
                sx={{
                  fontWeight: 600,
                  mb: 1,
                  color: "grey.900",
                  fontSize: "0.875rem",
                }}
              >
                {column.title}
              </Typography>
              <Stack spacing={0.5}>
                {column.links.map((link, linkIndex) => (
                  <Link
                    key={linkIndex}
                    href={link.href}
                    passHref
                    legacyBehavior
                  >
                    <Typography
                      component="a"
                      sx={{
                        color: "grey.600",
                        fontSize: "0.75rem",
                        textDecoration: "none",
                        "&:hover": {
                          color: "primary.main",
                          textDecoration: "underline",
                        },
                        cursor: "pointer",
                      }}
                    >
                      {link.label}
                    </Typography>
                  </Link>
                ))}
              </Stack>
            </Grid>
          ))}
        </Grid>

        <Divider sx={{ mb: 4 }} />

        {/* Contact & Newsletter Section */}
        <Box
          sx={{
            display: "flex",
            flexDirection: { xs: "column", md: "row" }, // Use responsive values
            justifyContent: "space-between",
            alignItems: { xs: "flex-start", md: "center" },
            gap: 4,
            py: 3,
          }}
        >
          {/* Contact Info */}
          <Box
            sx={{
              display: "flex",
              alignItems: "center",
              width: { xs: "100%", md: "auto" },
            }}
          >
            <Box
              sx={{
                width: 48,
                height: 48,
                flexShrink: 0,
                display: "flex",
                alignItems: "center",
                justifyContent: "center",
              }}
            >
              <Box
                component="img"
                src="/logo without name.png"
                alt="Logo"
                sx={{
                  width: "100%",
                  height: "100%",
                  objectFit: "contain",
                }}
              />
            </Box>
            <Box sx={{ ml: 2 }}>
              <Stack spacing={0.5}>
                <Typography
                  sx={{
                    display: "flex",
                    alignItems: "center",
                    gap: 0.5,
                    color: "grey.600",
                    fontSize: "0.75rem",
                  }}
                >
                  <Phone sx={{ fontSize: "0.875rem" }} />
                  +123456789
                </Typography>
                <Link href="mailto:email@agency.com" passHref legacyBehavior>
                  <Typography
                    component="a"
                    sx={{
                      display: "flex",
                      alignItems: "center",
                      gap: 0.5,
                      color: "grey.900",
                      fontSize: "0.75rem",
                      fontWeight: 700,
                      textDecoration: "none",
                      "&:hover": {
                        color: "primary.main",
                      },
                      cursor: "pointer",
                    }}
                  >
                    <Email sx={{ fontSize: "0.875rem" }} />
                    email@agency.com
                  </Typography>
                </Link>
                <Link href="/contact-us" passHref legacyBehavior>
                  <Typography
                    component="a"
                    sx={{
                      display: "flex",
                      alignItems: "center",
                      gap: 0.5,
                      color: "grey.900",
                      fontSize: "0.75rem",
                      fontWeight: 700,
                      textDecoration: "none",
                      "&:hover": {
                        color: "primary.main",
                      },
                      cursor: "pointer",
                    }}
                  >
                    <ContactMail sx={{ fontSize: "0.875rem" }} />
                    Contact Us
                  </Typography>
                </Link>
              </Stack>
            </Box>
          </Box>

          {/* Newsletter Signup */}
          <Box
            sx={{
              width: { xs: "100%", md: "auto" },
              flexGrow: { xs: 1, md: 0 },
            }}
          >
            <Stack
              direction={{ xs: "column", sm: "row" }} // Use responsive direction
              spacing={2}
              sx={{ width: "100%" }}
            >
              <TextField
                placeholder="Name"
                size="small"
                sx={{
                  flex: 1,
                  "& .MuiOutlinedInput-root": {
                    borderRadius: 1,
                    backgroundColor: "white",
                    "&:hover .MuiOutlinedInput-notchedOutline": {
                      borderColor: "grey.700",
                    },
                  },
                }}
              />
              <TextField
                placeholder="Email"
                type="email"
                size="small"
                sx={{
                  flex: 1,
                  "& .MuiOutlinedInput-root": {
                    borderRadius: 1,
                    backgroundColor: "white",
                    "&:hover .MuiOutlinedInput-notchedOutline": {
                      borderColor: "grey.700",
                    },
                  },
                }}
              />
              <Button
                variant="contained"
                sx={{
                  borderRadius: 1,
                  px: 4,
                  py: 1,
                  fontSize: "0.75rem",
                  fontWeight: 500,
                  whiteSpace: "nowrap",
                  height: 40,
                }}
              >
                Signup Newsletter
              </Button>
            </Stack>
          </Box>
        </Box>

        <Divider sx={{ mb: 4 }} />

        {/* Bottom Section */}
        <Box
          sx={{
            display: "flex",
            flexDirection: { xs: "column", md: "row" },
            justifyContent: "space-between",
            alignItems: { xs: "center", md: "center" },
            gap: 3,
            pt: 2,
          }}
        >
          {/* Copyright */}
          <Typography
            variant="body2"
            sx={{
              color: "grey.800",
              fontSize: "0.75rem",
              textAlign: { xs: "center", md: "left" },
            }}
          >
            © {new Date().getFullYear()} All Rights Reserved by PHPTRAVELS
          </Typography>

          {/* Powered By */}
          <Box
            sx={{
              display: "flex",
              alignItems: "center",
              gap: 1,
              color: "grey.500",
              fontSize: "0.875rem",
            }}
          >
            <Typography variant="body2" sx={{ fontSize: "0.75rem" }}>
              Powered by
            </Typography>
            <Box sx={{ display: "flex", alignItems: "center", gap: 1 }}>
              <Box
                component="img"
                src="/logo without name.png"
                alt="PHPTRAVELS"
                sx={{ width: 20, height: 20 }}
              />
              <Typography
                sx={{
                  fontSize: "0.75rem",
                  fontWeight: 700,
                  color: "grey.900",
                }}
              >
                PHPTRAVELS
              </Typography>
              <Typography
                sx={{ fontSize: "0.75rem", color: "grey.600" }}
              >
                v3.1
              </Typography>
            </Box>
          </Box>

          {/* Social Icons */}
          <Stack direction="row" spacing={2}>
            {socialIcons.map((social, index) => (
              <IconButton
                key={index}
                size="small"
                sx={{
                  color: "grey.600",
                  "&:hover": {
                    color: social.color,
                    backgroundColor: "rgba(0, 0, 0, 0.04)",
                  },
                }}
              >
                {social.icon}
              </IconButton>
            ))}
          </Stack>
        </Box>
      </Container>
    </Box>
  );
};ZZZ

export default Footer;